.navigation nav {
  background: #603bbb;
  width: 100%;
  padding: 20px 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navigation nav h2 {
  color: #fff;
}

.navigation nav button {
  width: 86px;
  padding: 8px 0;
  /* background: #3a2b58;   */
}

.navigation__Button {
  text-transform: initial;
  color: white;
}

.dropbtn {
  display: inline-block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-content a,
button {
  background-color: #603bbb;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
  color: white;
}

.dropdown-content a:hover,
.dropdown-content button:hover {
  opacity: 0.9;
  cursor: pointer;
}

.dropdown:hover .dropdown-content {
  display: block;
}
